@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, 'Roboto', 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code,
.code {
  font-family: "Roboto Mono", source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Sticky footer :D */
html, body, #root {
  /* Minimun height! */
  height: 100%;
}

#root > footer {
  /* Forces the footer to 'recoil back' to the bottom of the window. */
  position: -webkit-sticky;
  position: sticky;
  top: 100vh;
}
body.color-scheme-light{background:#e4e4e4}body.color-scheme-dark{background:#171717}.App{width:calc(min(100%, 1000px));transition:padding 100ms ease;padding-top:4.25rem}@media screen and (max-width: 750px){.App{padding-left:0;padding-right:0}}.card{border-radius:1rem;margin-bottom:.75rem;border:none}footer{display:flex;flex-direction:column}.App-header{transition:top 200ms ease;color:white;background-color:rgba(var(--bs-primary-rgb), 1) !important}.App-header.hidden{top:-3.5rem}.App-header .darkmode-switch input{box-shadow:0 0 0 2px rgba(255,255,255,0.5)}.click-target{cursor:pointer}

:root{--card-radius: 1rem;--btn-radius: .45rem}

body[color-scheme-light]{--th-gray-50: $gray-50-alt;--th-gray-100: $gray-100-alt;--th-gray-200: $gray-200-alt;--th-gray-300: $gray-300-alt;--th-gray-400: $gray-400-alt;--th-gray-500: $gray-500-alt;--th-gray-600: $gray-600-alt;--th-gray-700: $gray-700-alt;--th-gray-800: $gray-800-alt;--th-gray-900: $gray-900-alt}body[color-scheme-dark]{--th-gray-50: $gray-950-alt;--th-gray-100: $gray-900-alt;--th-gray-200: $gray-800-alt;--th-gray-300: $gray-700-alt;--th-gray-400: $gray-600-alt;--th-gray-500: $gray-500-alt;--th-gray-600: $gray-400-alt;--th-gray-700: $gray-300-alt;--th-gray-800: $gray-200-alt;--th-gray-900: $gray-100-alt}

.pensum-table-container .card-title{margin:1rem 0;font-size:Clamp(1.25rem, 5vmin, 2.5rem);text-transform:capitalize}.row-period{--bg-even: rgba(var(--bs-body-color-rgb), 0.1)}.even{background:var(--bg-even)}.row-period.row{--border-color: rgba(var(--bs-body-color-rgb), 0.5);border-top:1.5px solid var(--border-color)}.pensum-header+.row-period.row{border-top:none}.row-period-num{width:2rem;flex:0 0 auto;display:flex;align-items:center;justify-content:center}.row-period-num.click-target{background:linear-gradient(90deg, transparent 0%, var(--bg-even) 100%)}.row-period-num.click-target:hover{background:var(--bs-primary);color:white;cursor:pointer}.pensum-header{background:rgba(var(--bs-body-bg-rgb), 1);border-bottom:2px solid rgba(var(--bs-body-color-rgb), 1);border-top:none !important;position:-webkit-sticky;position:sticky;top:-2px;font-weight:bold}.row-code,.row-name,.row-cr{flex:0 0 auto;display:flex;align-items:center;padding-left:0;padding-right:0}.row-code{justify-content:center;width:100px;padding:.1rem 0}.row-cr{width:25px}.row-req{flex:1 3;display:flex;align-items:center;flex-wrap:wrap}.row-name{width:65%}@media screen and (max-width: 475px){.row-req{flex:0 0 auto}.row-name{flex:1 0 auto}}.pensum-table{max-width:inherit}@media screen and (max-width: 750px){.pensum-table-container{padding-left:0;padding-right:0}.pensum-table-container .card-title{padding:.75rem}}.pensum-header .col,.pensum-header .row{align-items:end}.row-cr{white-space:nowrap}.row-mat{background-color:var(--rowmat-bg)}

.mat-code{display:inline-block;background:#2a42ac;background:var(--mat-bg);color:var(--mat-fg);border-radius:5px;padding:0 .5rem;margin:.1rem}.mat-code.req-text{background-color:#4685fc}

