r-scheme-dark .passed:active,.color-scheme-dark .passed.active{--mat-bg: #00d900}.color-scheme-dark .passed:disabled,.color-scheme-dark .passed.disabled,.color-scheme-dark .passed.not-active{--mat-bg: #696}.color-scheme-dark .passed.row-mat{--rowmat-bg: rgba(0,255,0,0.3)}.color-scheme-dark .passed.row-mat.even{--rowmat-bg: rgba(0,255,0,0.2)}.color-scheme-dark .course{--mat-bg: #bf9540;--mat-fg: #fff}.color-scheme-dark .course:hover,.color-scheme-dark .course.hover{--mat-bg: #8f743d}.color-scheme-dark .course:active,.color-scheme-dark .course.active{--mat-bg: #d99100}.color-scheme-dark .course:disabled,.color-scheme-dark .course.disabled,.color-scheme-dark .course.not-active{--mat-bg: #986}.color-scheme-dark .course.row-mat{--rowmat-bg: rgba(255,170,0,0.3)}.color-scheme-dark .course.row-mat.even{--rowmat-bg: rgba(255,170,0,0.2)}.color-scheme-dark .missing{--mat-bg: #bf4040;--mat-fg: #fff}.color-scheme-dark .missing:hover,.color-scheme-dark .missing.hover{--mat-bg: #8f3d3d}.color-scheme-dark .missing:active,.color-scheme-dark .missing.active{--mat-bg: #d90000}.color-scheme-dark .missing:disabled,.color-scheme-dark .missing.disabled,.color-scheme-dark .missing.not-active{--mat-bg: #966}.color-scheme-dark .missing.row-mat{--rowmat-bg: rgba(255,0,0,0.3)}.color-scheme-dark .missing.row-mat.even{--rowmat-bg: rgba(255,0,0,0.2)}

.filter-selector .btn {
  background-color: var(--mat-bg);
  color: var(--mat-fg);
  border: none;
}
.filter-selector .active,
.filter-selector .active:focus {
  box-shadow: inset 0 0 0 3px var(--mat-fg);
  z-index: 0 !important; /* Without this, this btn would overlap the selector. */
}
.darkmode-switch{display:grid;grid-template-rows:1fr;grid-template-columns:1fr;-webkit-transform:rotate(0deg) scale(1.5);transform:rotate(0deg) scale(1.5);color:#ffaa00;transition:-webkit-transform 700ms ease !important;transition:transform 700ms ease !important;transition:transform 700ms ease, -webkit-transform 700ms ease !important}.darkmode-switch>*{grid-row:1 / 2;grid-column:1 / 2}.darkmode-switch.dark{-webkit-transform:rotate(-360deg) scale(1.5);transform:rotate(-360deg) scale(1.5)}.darkmode-switch input{display:absolute;top:0;opacity:0}.darkmode-switch input{background-color:#203864}.color-scheme-transition,.color-scheme-transition *{transition:background-color 200ms ease}

